/* PAGE TRANSITION */
div.transition-group {
     height: 100%;
     width: 100%;
}
section.route-section {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.fadeIn-enter {
    -webkit-transform: translateX(100%);
        -ms-transform: translateX(100%);
            transform: translateX(100%);
    z-index: 1
}

.fadeIn-enter.fadeIn-enter-active {
    -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
            transform: translateX(0);
    -webkit-transition: -webkit-transform 1000ms cubic-bezier(.4,.01,.13,.99);
    transition: -webkit-transform 1000ms cubic-bezier(.4,.01,.13,.99);
    -o-transition: transform 1000ms cubic-bezier(.4,.01,.13,.99);
    transition: transform 1000ms cubic-bezier(.4,.01,.13,.99);
    transition: transform 1000ms cubic-bezier(.4,.01,.13,.99), -webkit-transform 1000ms cubic-bezier(.4,.01,.13,.99);
    z-index: 1

}

.fadeIn-exit {
    -webkit-transform: translateX(100%);
        -ms-transform: translateX(100%);
            transform: translateX(100%);
    -webkit-transition: -webkit-transform 1000ms cubic-bezier(.4,.01,.13,.99);
    transition: -webkit-transform 1000ms cubic-bezier(.4,.01,.13,.99);
    -o-transition: transform 1000ms cubic-bezier(.4,.01,.13,.99);
    transition: transform 1000ms cubic-bezier(.4,.01,.13,.99);
    transition: transform 1000ms cubic-bezier(.4,.01,.13,.99), -webkit-transform 1000ms cubic-bezier(.4,.01,.13,.99);
    z-index: 0;

}

.fadeIn-exit.fade-exit-active {
    -webkit-transform: translateX(-100%);
        -ms-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 1000ms cubic-bezier(.4,.01,.13,.99);
    transition: -webkit-transform 1000ms cubic-bezier(.4,.01,.13,.99);
    -o-transition: transform 1000ms cubic-bezier(.4,.01,.13,.99);
    transition: transform 1000ms cubic-bezier(.4,.01,.13,.99);
    transition: transform 1000ms cubic-bezier(.4,.01,.13,.99), -webkit-transform 1000ms cubic-bezier(.4,.01,.13,.99);
    z-index: 0;

}



